.configuration-icon-background {
    display: block;
    height: 15px;
    width: 19px;
    text-indent: -9999px;
    background: url(/assets/svg-icons/configure_widget_icon.svg) no-repeat top left;
    background-size: 20px 15px;
}

.input-icon {
    display: block;
    height: 15px;
    width: 20px;
    text-indent: -9999px;
    background: url(/assets/svg-icons/ic_input_24px.svg) no-repeat top left;
    background-size: 20px 15px;
}

.unbarred::before {
    width: 16px;
    height: 16px;
    content: url('/assets/barring-status/unbarred_icon.svg');
}

.barred::before {
    width: 16px;
    height: 16px;
    content: url('/assets/barring-status/barred_icon.svg');
}

