body .p-confirm-dialog.p-dialog .p-dialog-content {
    display: flex;
    align-items: center;
}

body .p-confirm-dialog-message {
    white-space: pre-line;
}

body .p-confirm-dialog {
    width: 30em;

    .p-dialog-content .p-confirm-dialog-icon {
        float: left;
        top: inherit;
        position: static;
        font-size: 32px;

        &.confirm-icon {
            color: #FF7300;
        }
    }
}
