@import '../nokia-variables';

body {
    .ui-tree {
        width: 100%;
        max-width: 320px;
        height: 100%;
        overflow: auto;
        border: none;
        background-color: transparent;
        color: $panelContentTextColor;
        padding: 0;

        .ui-tree-container {
            padding: 2px;
            overflow: visible;
            margin: 0;

            .ui-treenode {
                padding: $treeNodePadding;

                .ui-treenode-content {
                    padding: 0;
                    border: 1px solid transparent;

                    &:not(.p-disabled):hover {
                        background: $nokiaUiLightBlue1Color;
                    }

                    .ui-tree-toggler {
                        vertical-align: middle;
                        display: inline-block;
                        float: none;
                        margin: 0 $treeNodeContentSpacing 0 0;
                        color: $nokiaUiBlackColor;
                    }

                    .ui-treenode-icon {
                        vertical-align: middle;
                        display: inline-block;
                        margin: 0 $treeNodeContentSpacing 0 0;
                    }

                    .ui-treenode-label {
                        margin: 0;
                        vertical-align: middle;
                        display: inline-block;
                        padding-left: 8px;

                        &.p-highlight {
                            color: $nokiaDarkBlueColor;
                            background-color: transparent;
                            font-weight: bold;
                        }
                    }

                    .p-checkbox {
                        margin: 0;

                        .p-checkbox-icon {
                            margin: 0;
                        }

                        .pi-minus {
                            color: $nokiaUiWhiteColor;
                            background-color: $nokiaDarkBlueColor;
                            margin-left: -1px;
                            margin-top: -1px;
                            height: $nokiaCheckboxHeight;
                            width: $nokiaCheckboxHeight;
                            border: $nokiaDarkBlueColor;
                            border-radius: 3px;

                            &:not(.p-disabled):hover {
                                border: $nokiaDarkerBlueColor;
                                background-color: $nokiaDarkerBlueColor;
                            }
                        }
                    }

                    &.ui-treenode-selectable {
                        .ui-treenode-label:not(.p-highlight):hover {
                            background-color: $nokiaUiLightBlue1Color;
                            color: $nokiaUiBlackColor;
                        }

                        &:focus {
                            outline-color: $nokiaDarkerBlueColor;
                        }
                    }
                }
            }
        }
    }
}
