@import './nokia-variables';
@import './extensions/toastr';

/* based on angular-toastr css https://github.com/Foxandxss/angular-toastr/blob/cb508fe6801d6b288d3afc525bb40fee1b101650/dist/angular-toastr.css */

/* position */
.toast-center-center {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.toast-top-center {
    top: 0;
    right: 0;
    width: 100%;
}
.toast-bottom-center {
    bottom: 0;
    right: 0;
    width: 100%;
}
.toast-top-full-width {
    top: 0;
    right: 0;
    width: 100%;
}
.toast-bottom-full-width {
    bottom: 0;
    right: 0;
    width: 100%;
}
.toast-top-left {
    top: 12px;
    left: 12px;
}
.toast-top-right {
    top: 12px;
    right: 12px;
}
.toast-bottom-right {
    right: 30px;
    bottom: 12px;
}
.toast-bottom-left {
    bottom: 12px;
    left: 12px;
}

/* toast styles */
.toast-title {
    font-size: 14px;
    font-weight: bold;
    color: $nokiaUiGray4Color;
    line-height: normal;
}
.toast-message {
    font-size: 14px;
    word-wrap: break-word;
    color: $nokiaUiGray4Color;
    line-height: normal;
}
.toast-message a,
.toast-message label {
    color: #FFFFFF;
}
.toast-message a:hover {
    color: #CCCCCC;
    text-decoration: none;
}
.toast-close-button {
    position: relative;
    right: -0.3em;
    top: -0.3em;
    float: right;
    font-size: 20px;
    font-weight: bold;
    color: #FFFFFF;
    text-shadow: 0 1px 0 #ffffff;
    /* opacity: 0.8; */
}
.toast-close-button:hover,
.toast-close-button:focus {
    color: #000000;
    text-decoration: none;
    cursor: pointer;
    opacity: 0.4;
}
/*Additional properties for button version
 iOS requires the button element instead of an anchor tag.
 If you want the anchor version, it requires `href="#"`.*/
button.toast-close-button {
    padding: 0;
    cursor: pointer;
    background: transparent;
    border: 0;
}

.toast-container {
    pointer-events: none;
    position: fixed;
    z-index: 999999;
}

.toast-container * {
    box-sizing: border-box;
}

.toast-container .ngx-toastr {
    max-width: 350px;
    position: relative;
    overflow: visible;
    padding: 15px 47px;
    background-position: 11px 11px;
    background-repeat: no-repeat;
    background-size: $nokiaDefaultInformationIconSize;
    box-shadow: $nokiaDefaultToastShadow;
    transition: box-shadow $nokiaDefaultTransitionDuration ease-in-out;
    color: black;
    display: block;
    border-radius: $nokiaDefaultBorderRadius;

    &::before {
        content: ' ';
        position: absolute;
        top: -5px;
        right: 0px;
        left: 0px;
        height: 11px;
        z-index: -2;
        border-radius: $nokiaDefaultBorderRadius;
    }

    &::after {
        content: ' ';
        position: absolute;
        top: 0px;
        right: 0px;
        left: 0px;
        height: 6px;
        z-index: -1;
        border-radius: $nokiaDefaultBorderRadius;
        background-color: white;
    }

    &:not(:last-child) {
        margin-bottom: 20px;
    }
}

.toast-container .ngx-toastr:hover {
    box-shadow: $nokiaHoverToastShadow;
    opacity: 1;
    cursor: pointer;
}

.toast-info {
    background-image: url(/assets/svg-icons/information_icon_blue.svg);

    &::before {
        background-color: $nokiaToastBlueColor;
    }
}

.toast-error {
    background-image: url(/assets/svg-icons/error_icon.svg);

    &::before {
        background-color: $nokiaToastRedColor;
    }
}

.toast-success {
    background-image: url(/assets/svg-icons/success_icon.svg);

    &::before {
        background-color: $nokiaToastGreenColor;
    }
}

.toast-warning {
    background-image: url(/assets/svg-icons/warning_icon.svg);

    &::before {
        background-color: $nokiaToastOrangeColor;
    }
}

.toast-container.toast-top-center .ngx-toastr,
.toast-container.toast-bottom-center .ngx-toastr {
    width: 300px;
    margin-left: auto;
    margin-right: auto;
}
.toast-container.toast-top-full-width .ngx-toastr,
.toast-container.toast-bottom-full-width .ngx-toastr {
    width: 96%;
    margin-left: auto;
    margin-right: auto;
}
.ngx-toastr {
    background-color: white;
    pointer-events: auto;

    -webkit-box-shadow: 0px 1.5px 3px 0px rgba(0, 0, 0, 0.4);
    -moz-box-shadow: 0px 1.5px 3px 0px rgba(0, 0, 0, 0.4);
    box-shadow: 0px 1.5px 3px 0px rgba(0, 0, 0, 0.4);
}

.toast-progress {
    position: absolute;
    left: 0;
    bottom: 0;
    height: 4px;
    background-color: #000000;
    opacity: 0.4;
}
/* Responsive Design */
@media all and (max-width: 240px) {
    .toast-container .ngx-toastr.div {
        padding: 8px 8px 8px 50px;
        width: 11em;
    }
    .toast-container .toast-close-button {
        right: -0.2em;
        top: -0.2em;
    }
}
@media all and (min-width: 241px) and (max-width: 480px) {
    .toast-container .ngx-toastr.div {
        padding: 8px 8px 8px 50px;
        width: 18em;
    }
    .toast-container .toast-close-button {
        right: -0.2em;
        top: -0.2em;
    }
}
@media all and (min-width: 481px) and (max-width: 768px) {
    .toast-container .ngx-toastr.div {
        padding: 15px 15px 15px 50px;
        width: 25em;
    }
}
