@import 'theme/nokia-variables';


.expandable-chips-box {
    border: 1px solid $nokiaGray3Color;
    border-radius: 3px;

    &.p-chips {
        width: 100%;
        height: 100%;
    }

    &.p-chips > ul.p-inputtext {
        overflow: auto;
        padding: 8px;
        width: 100%;
        height: 100%;
        border: 0;
        border-radius: 0;
        background-color: transparent;

        .p-chips-token {
            position: relative;
            padding: 5px 32px 5px 10px;
            line-height: 12px;
            margin-right: 5px;
            margin-bottom: 5px;
            border-radius: 33px;
            font-size: 11px;
            height: 24px;

            &:not(:hover) {
                border: 1px solid $nokiaDarkBlueColor;
            }

            &:hover {
                cursor: pointer;
                border: 1px solid $nokiaLightBlueColor;
            }

            .p-chips-token-icon {
                position: absolute;
                margin-right: 10px;
                top: 5px;
                margin-top: 0;
                width: 14px;
                height: 14px;
                border-radius: 0;
                color: $nokiaDarkBlueColor;
                background-color: $nokiaUiWhiteColor;

                &:hover {
                    color: $nokiaLightBlueColor;
                    background-color: $nokiaUiWhiteColor;
                }

                &::before {
                    font-family: 'Material Design Icons', monospace;
                    content: $nokiaMDICloseCircleIconContent;
                    font-size: 14px;
                }
            }
        }

        &:hover:not(.p-disabled) {
            border: 0;
            background-color: transparent;
        }

        > .p-chips-input-token {
            padding: 0;
            width: 100%;

            > input {
                margin: 0;
            }

            input.input-hidden {
                display: none;
                pointer-events: none;
            }

            input.input-visible {
                width: 100%;
                display: inline-block;
                pointer-events: none;
            }
        }
    }
}

.vertical-chip-box {
    padding: 1px 1px 0 0;
    width: 100%;

    &.p-chips {
        height: 100%;
    }

    &.p-chips > ul.p-inputtext {
        display: flex;
        flex-flow: wrap;
        align-items: flex-start;
        overflow: hidden;
        height: 100%;
        border: 0;
        border-radius: 0;
        background-color: transparent;

        .p-chips-token {
            padding: 5px 32px 5px 10px;
            line-height: 12px;
            margin-right: 5px;
            margin-bottom: 5px;
            border-radius: 33px;
            font-size: 11px;
            height: 24px;
            max-width: 100%;
            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap;
            flex-shrink: 0;

            &:not(:hover) {
                border: 1px solid $nokiaDarkBlueColor;
            }

            &:hover {
                cursor: pointer;
                border: 1px solid $nokiaLightBlueColor;
            }

            .p-chips-token-icon {
                margin-right: 10px;
                margin-top: -7px;
                width: 14px;
                height: 14px;
                border-radius: 0;
                color: $nokiaDarkBlueColor;
                background-color: $nokiaUiWhiteColor;

                &:hover {
                    color: $nokiaLightBlueColor;
                    background-color: $nokiaUiWhiteColor;
                }

                &::before {
                    font-family: 'Material Design Icons', monospace;
                    content: $nokiaMDICloseCircleIconContent;
                    font-size: 14px;
                }
            }
        }

        &:hover:not(.p-disabled) {
            border: 0;
            background-color: transparent;
        }

        > .p-chips-input-token {
            padding: 0;
            width: 100%;

            > input {
                margin: 0;
            }

            input.input-hidden {
                display: none;
                pointer-events: none;
            }

            input.input-visible {
                width: 100%;
                display: inline-block;
                pointer-events: none;
            }
        }
    }
}
