@import '../nokia-variables';

$defaultButtonVerticalPadding: 0.455em;
$thinButtonVerticalPadding: 0.273em;
$thickButtonVerticalPadding: 0.755em;

$defaultButtonHorizontalPadding: 1.545em;
$shortButtonHorizontalPadding: 0.728em;
$longButtonHorizontalPadding: 2.318em;

$defaultButtonIconOnlyPadding: 0.455em;
$shortButtonIconOnlyPadding: 0.455em;
$longButtonIconOnlyPadding: 0.728em;

$defaultButtonPaddingOffsetForIcon: 15px;
$shortButtonPaddingOffsetForIcon: 20px;
$shortButtonPaddingOffsetForBigIcon: 22px;
$longButtonPaddingOffsetForIcon: 11px;

$defaultButtonIconOffset: 11px;
$shortButtonIconOffset: 8px;
$shortButtonIconOffsetBigIcon: 5px;
$longButtonIconOffset: 11px;

@mixin generateVerticalPaddings($padding: 0.455em) {
    &.p-button,
    &.button-icon-left,
    &.button-icon-right,
    &.p-button-icon-only {
        .p-button-label {
            padding-top: $padding;
            padding-bottom: $padding;
        }
    }
}

@mixin generateHorizontalPaddings($padding, $iconOnlyPadding, $paddingOffsetForIcon, $iconOffset) {
    &.p-button .p-button-label {
        padding-left: $padding;
        padding-right: $padding;
    }

    &.button-icon-left .p-button-label {
        padding-left: calc(#{$padding} + #{$paddingOffsetForIcon});
        padding-right: $padding;
    }

    &.button-icon-right .p-button-label {
        padding-left: $padding;
        padding-right: calc(#{$padding} + #{$paddingOffsetForIcon});
    }

    &.p-button-icon-only .p-button-label {
        // position: absolute;
        padding-left: $iconOnlyPadding;
        padding-right: $iconOnlyPadding;
    }

    &.button-icon-left .p-button-icon-left {
        position: absolute;
        left: $iconOffset;
        height: 1em;
        line-height: $nokiaControlIconLineHeight;
        top: 50%;
        margin-top: -0.5em;
    }

    &.button-icon-right .p-button-icon-right {
        position: absolute;
        right: $iconOffset;
    }
}

body {
    .p-selectbutton .ui-button,
    .p-button {
        display: inline-block;
        font-family: $nokiaDefaultFontFace;
        font-size: $nokiaControlTextFontSize;
        color: $nokiaUiDefaultTextColor;
        background-color: $nokiaUiWhiteColor;
        box-shadow: $nokiaDefaultBoxShadow;
        border: 1px solid $nokiaGray3Color;
        border-radius: 3px;
        margin: 0;
        padding: 0;
        outline: 0 none;
        outline-offset: 0;
        align-items: flex-start;

        &:enabled {
            &:focus {
                color: $nokiaUiDefaultTextColor;
                background-color: $nokiaUiWhiteColor;
                border-color: $nokiaUiGray4Color;
                box-shadow: $nokiaDefaultBoxShadow;
            }

            &:hover {
                color: $nokiaUiDefaultTextColor;
                background-color: $nokiaUiHoverBgBlueColor;
                border-color: $nokiaLightBlueColor;
            }

            &:active {
                color: $nokiaUiDefaultTextColor;
                background-color: $nokiaUiHoverBgBlueColor;
                border-color: $nokiaLightBlueColor;
                box-shadow: $nokiaActiveBoxShadow;
                outline: 0 none;
                outline-offset: 0;
            }
        }

        &.p-disabled,
        &:disabled {
            color: $nokiaGray2Color;
            background-color: $nokiaUiWhiteColor;
            border: 1px solid $nokiaGray2Color;
        }

        .p-button-label {
            display: block;
            line-height: $nokiaControlTextLineHeight;
            white-space: nowrap;
        }

        .mdi-21px-bold + .p-button-label {
            font-weight: $nokiaHeaderFontWeight;
        }

        &.p-button-icon-only .p-button-icon {
            line-height: $nokiaControlIconLineHeight;
            position: absolute;
            top: 50%;
            left: 50%;
            margin-top: -0.5em;
            margin-left: -0.5em;
            width: 1em;
            font-size: 18px;
            height: 1em;
        }

        &.button-icon-left {
            position: relative;

            .p-button-icon-left {
                font-size: $nokiaDefaultIconFontSize;

                &.mdi-18px {
                    font-size: 18px;
                }

                &.mdi-21px-bold {
                    font-size: 21px;
                }
            }
        }

        &.button-icon-right {
            position: relative;

            .p-button-icon-right {
                font-size: $nokiaDefaultIconFontSize;
            }
        }

        &.p-button-icon-only {
            width: unset;

            .p-button-icon-left, .p-button-icon-right {
                font-size: $nokiaControlIconOnlyFontSize;
            }

            .p-button-label {
                width: $nokiaControlTextLineHeight;
                height: $nokiaControlTextLineHeight;
                text-indent: 0;
                visibility: hidden;
                box-sizing: content-box;
            }
        }

        @include generateVerticalPaddings($defaultButtonVerticalPadding);
        @include generateHorizontalPaddings(
            $defaultButtonHorizontalPadding,
            $defaultButtonIconOnlyPadding,
            $defaultButtonPaddingOffsetForIcon,
            $defaultButtonIconOffset
        );
    }

    .p-selectbutton {
        .p-button {
            &:not(.p-disabled):not(.p-highlight):focus {
                color: $nokiaUiDefaultTextColor;
                background-color: $nokiaUiWhiteColor;
                border-color: $nokiaUiGray4Color;
                box-shadow: $nokiaDefaultBoxShadow;
            }

            &:not(.p-disabled):not(.p-highlight):hover {
                color: $nokiaUiDefaultTextColor;
                background-color: $nokiaUiHoverBgBlueColor;
                border-color: $nokiaLightBlueColor;
            }

            &:not(.p-disabled).p-highlight {
                background-color: $nokiaDarkBlueColor;
                color: $nokiaUiWhiteColor;
                box-shadow: $nokiaDefaultBoxShadow;
                border-color: transparent;
            }

            &:not(.p-disabled).p-highlight:focus {
                background-color: $nokiaDarkBlueColor;
            }

            &:not(.p-disabled).p-highlight:hover {
                background-color: $nokiaDarkerBlueColor;
                box-shadow: $nokiaDefaultBoxShadow;
                border-color: transparent;
            }

            &.p-disabled {
                color: $nokiaGray2Color;
                background-color: $nokiaUiWhiteColor;
                border: 1px solid $nokiaGray2Color;
            }

            &:last-child {
                border-bottom-left-radius: 0;
                border-top-left-radius: 0;
            }

            &:first-child {
                border-bottom-right-radius: 0;
                border-top-right-radius: 0;
                border-right: none;
            }

            &:not(:first-child):not(:last-child) {
                border-radius: 0;
                border-right: none;
            }
        }
    }


    .p-button.ui-button-short {
        @include generateHorizontalPaddings(
            $shortButtonHorizontalPadding,
            $shortButtonIconOnlyPadding,
            $shortButtonPaddingOffsetForIcon,
            $shortButtonIconOffset
        );
    }

    .p-button.ui-button-short-big-icon {
        @include generateHorizontalPaddings(
            $shortButtonHorizontalPadding,
            $shortButtonIconOnlyPadding,
            $shortButtonPaddingOffsetForBigIcon,
            $shortButtonIconOffsetBigIcon
        );
    }

    .p-button.ui-button-long {
        @include generateHorizontalPaddings(
            $longButtonHorizontalPadding,
            $longButtonIconOnlyPadding,
            $longButtonPaddingOffsetForIcon,
            $longButtonIconOffset
        );
    }

    .p-button.ui-button-thin {
        @include generateVerticalPaddings($thinButtonVerticalPadding);
    }

    .p-button.ui-button-thick {
        @include generateVerticalPaddings($thickButtonVerticalPadding);
    }


    p-togglebutton.ui-toogle-default {
        background-color: transparent;
        color: $nokiaUiDefaultTextColor;

        .p-button {
            width: $nokiaSecondaryButtonMinWidth;
            height: $nokiaSecondaryButtonInTableMinWidth;
            box-shadow: none;
            border: 1px solid transparent;

            &:not(.p-disabled):not(.p-highlight).p-focus {
                color: $nokiaUiWhiteColor;
                background-color: transparent;
                border-color: $nokiaUiGray4Color;
                box-shadow: none;
            }

            &:not(.p-disabled):not(.p-highlight):focus {
                color: $nokiaUiWhiteColor;
                background-color: transparent;
                border-color: $nokiaUiGray4Color;
                box-shadow: none;
            }

            &:not(.p-disabled):not(.p-highlight):hover {
                color: $nokiaUiLightBlue1Color;
                background-color: $nokiaUiLightBlue1Color;
                border-color: transparent;
                box-shadow: $nokiaDefaultBoxShadow;
            }

            &:not(.p-disabled).p-highlight {
                color: $nokiaUiLightBlue1Color;
                background-color: $nokiaAlarmFilterButtonBackgroundFocus;
                border-color: transparent;
                box-shadow: none;
            }

            &:not(.p-disabled).p-highlight:focus {
                background-color: $nokiaAlarmFilterButtonBackgroundFocus;
            }

            &:not(.p-disabled).p-highlight:hover {
                background-color: $nokiaAlarmFilterButtonBackgroundPressed;
                box-shadow: $nokiaDefaultBoxShadow;
                border-color: transparent;
            }

            &.p-disabled {
                color: $nokiaUiBlackColor;
                background-color: $nokiaUiWhiteColor;
            }
        }
    }

    .p-button.ui-button-secondary,
    .ui-buttonset.ui-button-secondary > .p-button.custom-button {
        background-color: transparent;
        color: $nokiaUiDefaultTextColor;
        box-shadow: none;
        border: 1px solid transparent;

        &:enabled {
            &:focus {
                color: $nokiaUiDefaultTextColor;
                background-color: transparent;
                border-color: $nokiaUiGray4Color;
                box-shadow: none;
            }

            &:hover {
                color: $nokiaUiDefaultTextColor;
                background-color: $nokiaUiLightBlue1Color;
                border-color: transparent;
                box-shadow: $nokiaDefaultBoxShadow;
            }

            &:active {
                color: $nokiaUiDefaultTextColor;
                background-color: $nokiaUiLightBlue1Color;
                border-color: transparent;
                box-shadow: $nokiaActiveBoxShadow;
            }
        }

        &:disabled {
            color: $nokiaGray2Color;
        }
    }

    .ui-button-accent {
        &:enabled {
            &:focus::after {
                content: ' ';
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                border: 1px solid $nokiaUiWhiteColor;
                border-radius: 2px;
            }

            &:active::after,
            &:hover::after {
                content: none;
            }
        }
    }

    .ui-buttonset.ui-button-accent > .p-button,
    .p-button.ui-button-accent {
        background-color: $nokiaDarkBlueColor;
        color: $nokiaUiWhiteColor;
        border: 1px solid transparent;
        box-shadow: $nokiaDefaultBoxShadow;

        &:enabled {
            &:focus {
                color: $nokiaUiWhiteColor;
                background-color: $nokiaDarkBlueColor;
                border-color: transparent;
            }

            &:hover {
                color: $nokiaUiWhiteColor;
                border: 1px solid transparent;
                background-color: $nokiaDarkerBlueColor;
                box-shadow: $nokiaDefaultBoxShadow;
                border-color: transparent;
            }

            &:active {
                color: $nokiaUiWhiteColor;
                background-color: $nokiaDarkerBlueColor;
                box-shadow: $nokiaActiveBoxShadow;
                border-color: transparent;
            }
        }

        &:disabled {
            background-color: $nokiaGray11Color;
        }
    }

    .button-indicated {
        .p-button {
            border-color: $nokiaDarkBlueColor;
            box-shadow: 0 0 0 1px $nokiaDarkBlueColor;

            &:enabled {
                &:focus {
                    border-color: $nokiaDarkBlueColor;
                    box-shadow: 0 0 0 1px $nokiaDarkBlueColor;
                }
            }
        }
    }
}
