@import '../_nokia-variables.scss';

body {
    .p-inputswitch {
        width: $nokiaInputSwitchWidth;
        height: $nokiaInputSwitchHeight;

        &.p-focus {
            .p-inputswitch-slider {
                background-color: $nokiaToastGrayColor;
            }

            .p-inputswitch-slider::before {
                border: 1px solid $nokiaUiGray4Color;
            }
        }

        .p-inputswitch-slider {
            background-color: $nokiaToastGrayColor;

            &::before {
                box-shadow: $nokiaInputSwitchShadow;
                background-color: $nokiaUiWhiteColor;
                height: 20px;
                width: 20px;
                left: 0;
                top: -3px;
                bottom: -3px;
                border-radius: 50%;
            }

            &::after {
                content: '';
                display: block;
                transition: opacity 0.4s ease-in;
                opacity: 0;
            }
        }

        &:focus {
            outline: none;
        }

        &:not(.p-disabled):hover {
            .p-inputswitch-slider {
                background-color: $nokiaToastGrayColor;
            }

            .p-inputswitch-slider::before {
                border: 1px solid $nokiaLightBlueColor;
            }
        }

        &:not(.p-disabled):active {
            .p-inputswitch-slider {
                background-color: $nokiaToastGrayColor;
            }

            .p-inputswitch-slider::before {
                background-color: $nokiaUiLightBlue1Color;
                border: none;
            }
        }

        &.p-inputswitch-checked {
            .p-inputswitch-slider {
                background-color: $nokiaGray8Color;
            }

            .p-inputswitch-slider::before {
                background-color: $nokiaToastBlueColor;
            }

            &:not(.p-disabled):hover {
                .p-inputswitch-slider {
                    background-color: $nokiaGray8Color;
                }

                .p-inputswitch-slider::before {
                    border: none;
                    background-color: $nokiaDarkerBlueColor;
                }
            }

            &:not(.p-disabled):active {
                .p-inputswitch-slider {
                    background-color: $nokiaGray8Color;
                }

                .p-inputswitch-slider::before {
                    box-shadow: $nokiaActiveInputSwitchShadow;
                    background-color: $nokiaDarkerBlueColor;
                }
            }

            &.p-focus {
                .p-inputswitch-slider {
                    background-color: $nokiaGray8Color;
                }

                .p-inputswitch-slider::before {
                    background-color: $nokiaToastBlueColor;
                    border: 0.5px solid $nokiaUiBlackColor;
                }

                .p-inputswitch-slider::after {
                    background-color: $nokiaToastBlueColor;
                    border: 1px solid $nokiaUiWhiteColor;
                    display: block;
                    width: 16px;
                    height: 16px;
                    border-radius: 50%;
                    position: absolute;
                    top: -1px;
                    left: 17px;
                    opacity: 1;
                }
            }

            &.p-disabled {
                .p-inputswitch-slider {
                    background-color: $nokiaGray2Color;
                }

                .p-inputswitch-slider::before {
                    background-color: $nokiaGray11Color;
                }
            }
        }

        &.p-disabled {
            .p-inputswitch-slider {
                background-color: $nokiaGray2Color;
            }

            .p-inputswitch-slider::before {
                background-color: $nokiaGray1Color;
            }
        }
    }
}
