@import '../nokia-variables';

body {
    .p-treetable-resizable > .p-treetable-wrapper {
        overflow: visible;
    }

    .p-treetable {
        box-shadow: $nokiaShallowBoxShadow;
        font-size: $nokiaControlTextFontSize;
        background-color: transparent;

        .filter-input {
            --input-height: 21px;
        }

        .p-inputgroup .p-inputgroup-addon i {
            line-height: 1;
        }

        .p-treetable-thead > tr > th,
        .p-treetable-tbody tr > td {
            padding: $uiTableCellPadding;
            height: $nokiaTableCellHeight;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            border-collapse: collapse;

            p-treeTableSortIcon {
                > .pi-sort-alt::before {
                    content: '\fa63';
                }

                > .pi-sort-amount-up-alt::before {
                    content: '\f360';
                    display: block;
                    transform: translateY(-0.2em);
                }

                > .pi-sort-amount-down::before {
                    content: '\f35d';
                    display: block;
                    transform: translateY(0.1em);
                }
            }

            p-treetabletoggler {
                height: 21px;
                display: block;
                float: left;

                .pi {
                    height: 21px;
                    padding-top: 1px;
                }

                .pi-chevron-down::before {
                    content: '\F35D';
                    font: 1.25em/1 'Material Design Icons', sans-serif;
                    color: $nokiaUiGray4Color;
                }

                .pi-chevron-right::before {
                    content: '\F35F';
                    font: 1.25em/1 'Material Design Icons', sans-serif;
                    color: $nokiaUiGray4Color;
                }
            }

            .p-treetable-toggler {
                height: initial;
                position: static;
                margin-top: 0;

                a {
                    i.pi {
                        height: 21px;
                    }
                }
            }
        }

        .p-treetable-thead {
            tr > th {
                border: $tableHeaderCellBorder;
            }

            .p-sortable-column.p-highlight .p-sortable-column-icon {
                color: $nokiaUiBlackColor;
            }

            > tr th {
                background-color: $nokiaUiWhiteColor;
                border-top: none;
            }

            .p-sortable-column.p-highlight {
                background-color: $nokiaUiWhiteColor;
                color: $nokiaDefaultCheckedTextColor;
            }

            > tr > th:first-of-type {
                border-left: none;
                border-top-left-radius: $nokiaDefaultBorderRadius;
            }

            > tr:first-of-type > th {
                border-bottom: $nokiaDefaultTableDarkerBorder;
            }

            > tr > th:last-of-type {
                border-right: none;
                border-top-right-radius: $nokiaDefaultBorderRadius;
            }
        }

        .p-treetable-tbody {
            td > span {
                line-height: $nokiaTableCellHeightWithoutPadding;
            }

            > tr > td:first-of-type {
                border-left: none;
            }

            > tr > td:last-of-type {
                border-right: none;
            }

            > tr:last-of-type > td {
                border-bottom: none;
            }

            > tr:first-of-type > td {
                border-top: none;
            }

            > tr:focus > td {
                border-bottom-color: transparent;
                border-top-color: transparent;
            }
        }

        .p-sortable-column .p-sortable-column-icon {
            vertical-align: text-top;
            font-family: $nokiaMDIFontFace;
            font-size: $nokiaMDIIconFontSize;
            line-height: 0.8;
            margin-left: -0.3em;
        }

        .p-treetable-loading-content {
            .p-treetable-loading-icon {
                font-size: 4em;
            }
        }
    }

    .p-treetable-tbody > tr:focus, .p-treetable-thead > tr > th:focus {
        outline: 1px solid $offBorderColor;
        outline-offset: -1px;
    }

    .p-treetable-tbody > tr:focus + tr {
        > td {
            border-top-color: transparent;
        }
    }

    .p-treetable-hoverable-rows.p-highlight,
    .p-sortable-column.p-highlight {
        background-color: $nokiaUiWhiteColor;
        color: $nokiaDefaultCheckedTextColor;
    }

    .p-treetable.p-sortable-column .p-treetable-tbody > tr,
    .p-treetable.p-treetable-hoverable-rows .p-treetable-tbody > tr {
        &:not(.p-highlight):hover {
            background-color: $nokiaUiLightBlue1Color;
        }

        &.p-highlight {
            background-color: $nokiaToastBlueColor;
        }
    }

    .p-treetable .p-sortable-column:not(.p-highlight):hover {
        background-color: $nokiaUiLightBlue1Color;
    }
}
