@import '../nokia-variables';

$uiTableCellPadding: calc((26px - 2em) / 2) 8px;
$cellHeightWithoutBorder: var(--table-default-cell-height, #{$nokiaTableCellHeight});

body .table-with-pagination-shadow-wrapper {
    box-shadow: $nokiaShallowBoxShadow;
}


body {
    p-table .no-p-table-head .p-datatable-thead {
        height: 0;
    }

    .p-datatable {
        box-shadow: $nokiaShallowBoxShadow;
        font-size: $nokiaControlTextFontSize;
        background-color: transparent;
        border-radius: $nokiaDefaultBorderRadius;

        .ui-editable-column input {
            font-family: 'Nokia Pure', sans-serif;
        }

        .ui-column-resizer {
            right: -2px;
        }

        .p-datatable-tbody > tr:focus,
        .p-datatable-thead > tr > th:focus {
            outline: 1px solid $nokiaUiGray4Color;
            outline-offset: -1px;
        }

        .p-datatable-thead {
            height: var(--table-default-cell-height, $nokiaTableCellHeight);
            border-top: none;

            > tr > th {
                background-color: $nokiaUiWhiteColor;
                height: var(--table-default-cell-height, $nokiaTableCellHeight);
                padding: $uiTableCellPadding;
                border: $nokiaDefaultTableCellBorder;
                border-bottom: $nokiaDefaultTableDarkerBorder;
                border-top: none;
                text-overflow: ellipsis;

                p-sorticon {
                    > .pi-sort-alt::before {
                        content: '\fa63';
                    }

                    > .pi-sort-amount-up-alt::before {
                        content: '\f360';
                        display: block;
                        transform: translateY(-0.2em);
                    }

                    > .pi-sort-amount-down::before {
                        content: '\f35d';
                        display: block;
                        transform: translateY(0.1em);
                    }
                }
            }

            > tr > th:first-of-type {
                border-left: none;
                border-top-left-radius: $nokiaDefaultBorderRadius;
            }

            > tr > th:last-of-type {
                border-right: none;
                border-top-right-radius: $nokiaDefaultBorderRadius;
            }
        }

        .p-datatable-scrollable-header {
            border-bottom: $nokiaDefaultTableDarkerBorder;
            border-top-left-radius: $nokiaDefaultBorderRadius;
            border-top-right-radius: $nokiaDefaultBorderRadius;
            background-color: $nokiaUiWhiteColor;

            tr:last-child > th {
                border-bottom: none;
            }
        }

        .p-datatable-scrollable-header-box > .p-datatable-scrollable-header-table > .p-datatable-tbody > tr > td {
            border-bottom: $nokiaDefaultTableDarkerBorder;
        }

        .p-datatable-tbody {
            > tr.p-highlight {
                background-color: $nokiaToastBlueColor !important;
            }

            > tr > td {
                height: calc(#{$cellHeightWithoutBorder} + 1px);
                padding: $uiTableCellPadding;
                border: $nokiaDefaultTableCellBorder;
                text-overflow: ellipsis;
            }

            > tr:nth-child(even) {
                background-color: $nokiaUiWhiteColor;
            }

            > tr > td:first-of-type {
                border-left: none;
            }

            > tr > td:last-of-type {
                border-right: none;
            }

            > tr:last-of-type > td {
                border-bottom: none;
            }

            > tr:first-of-type > td {
                border-top: none;
            }
        }

        .p-datatable-scrollable-body {
            padding-bottom: 0 !important;
            border-bottom-left-radius: $nokiaDefaultBorderRadius;
            border-top-left-radius: 0;

            > .p-datatable-scrollable-body-table > .p-datatable-tbody {
                > tr:first-of-type > td, > tr:last-of-type > td {
                    height: $cellHeightWithoutBorder;
                }
            }

            .table > .p-datatable-tbody > tr:last-of-type > td {
                border-bottom: none;
            }
        }

        .p-datatable-unfrozen-view {
            .p-datatable-scrollable-body {
                border-bottom-left-radius: 0;
                border-bottom-right-radius: $nokiaDefaultBorderRadius;
            }

            .p-datatable-scrollable-header-table > .p-datatable-thead > tr > th:first-of-type {
                border-top-left-radius: 0;
            }
        }

        .p-datatable-frozen-view {
            border-right: $nokiaDefaultTableCellBorder;

            .p-datatable-scrollable-body > table > .p-datatable-tbody {
                tr:last-of-type > td {
                    padding-bottom: 0;
                    border-bottom: none;
                }
            }

            .p-datatable-scrollable-header-table .p-datatable-thead > tr > th {
                border-top-right-radius: 0;
            }
        }

        .ui-paginator-bottom {
            background-color: $nokiaUiWhiteColor;
            border-top: $nokiaDefaultTableCellBorder;
            border-bottom: none;
            border-left: none;
            border-right: none;
            border-bottom-left-radius: $nokiaDefaultBorderRadius;
            border-bottom-right-radius: $nokiaDefaultBorderRadius;
        }

        .p-sortable-column {
            .p-sortable-column-icon {
                color: $nokiaUiDefaultTextColor;
                font-family: $nokiaMDIFontFace;
                font-size: $nokiaMDIIconFontSize;
                line-height: 0.8;
                margin-left: -0.3em;
            }
        }

        .p-sortable-column.p-highlight {
            color: $nokiaUiGray4Color;
            background-color: $nokiaUiWhiteColor;

            .p-sortable-column-icon {
                color: $nokiaUiDefaultTextColor;
            }
        }

        .p-table-loading {
            background-color: black;
            opacity: 0.3;
            z-index: 50;
        }

        .p-datatable-loading-content {
            .p-datatable-loading-icon {
                font-size: 4em;
            }
        }

        .p-column-resizer-helper {
            background-color: $tableResizerHelperBgColor;
        }
    }

    .p-datatable.p-datatable-hoverable-rows .p-datatable-tbody > tr:not(.p-highlight):not(.ui-contextmenu-selected):hover {
        background-color: $nokiaUiLightBlue1Color !important;
    }

    .p-datatable .p-sortable-column:not(.p-highlight):hover {
        background-color: $nokiaUiLightBlue1Color;
    }
}

p-table {
    .p-datatable-scrollable {
        .p-datatable-table,
        .p-datatable-tbody,
        .p-datatable-tfoot,
        .p-datatable-thead {
            display: revert;

            tr {
                display: revert;

                td {
                    display: revert;
                }
            }
        }
    }
}
