@import 'theme/nokia-variables';

$spinner-size: 40px;

.data-loading-spinner {
    position: fixed;
    z-index: 9999;
    font-size: $spinner-size;
    width: $spinner-size;
    height: $spinner-size;
    margin: auto;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: visible;
    color: $nokiaGray3Color;

    &.embedded {
        position: absolute;
        color: $nokiaGray4Color;
    }

    &.embedded-no-background {
        position: absolute;
    }
}

.unlocked::before {
    content: '\FFE7';
    display: inline-block;
    font: normal normal normal 24px/1 'Material Design Icons', sans-serif;
    font-size: 16px;
    line-height: 16px;
}
