body {
    .ui-paginator {
        background-color: $paginatorBgColor;
        border: $paginatorBorder;
        padding: $paginatorPadding;

        .ui-paginator-first,
        .ui-paginator-prev,
        .ui-paginator-next,
        .ui-paginator-last {
            color: $paginatorIconColor;
            height: $paginatorElementHeight;
            min-width: $paginatorElementWidth;
            border: $paginatorElementBorder;
            line-height: $paginatorElementHeight;
            padding: $paginatorElementPadding;
            margin: $paginatorElementMargin;
            vertical-align: top;
            @include border-radius($paginatorElementBorderRadius);

            &:not(.p-disabled):not(.ui-state-active):hover {
                background-color: $paginatorElementHoverBgColor;
                color: $paginatorElementHoverIconColor;
            }
        }

        .ui-paginator-current {
            color: $paginatorIconColor;
            height: $paginatorElementHeight;
            min-width: $paginatorElementWidth;
            line-height: $paginatorElementHeight;
        }

        .ui-dropdown {
            border: 0 none;

            .ui-dropdown-trigger, .ui-dropdown-label {
                color: $paginatorIconColor;
            }

            &:hover {
                .ui-dropdown-trigger, .ui-dropdown-label {
                    color: $paginatorElementHoverIconColor;
                }
            }
        }

        .ui-paginator-first {
            &:before {
                position: relative;
                top: 1px;
            }
        }

        .ui-paginator-prev {
            &:before {
                position: relative;
            }
        }

        .ui-paginator-next {
            &:before {
                position: relative;
                top: 1px;
            }
        }

        .ui-paginator-last {
            &:before {
                position: relative;
                top: 1px;
            }
        }

        .ui-paginator-pages {
            vertical-align: top;
            display: inline-block;
            padding: 0;

            .ui-paginator-page {
                color: $paginatorIconColor;
                height: $paginatorElementHeight;
                min-width: $paginatorElementWidth;
                border: $paginatorElementBorder;
                line-height: $paginatorElementHeight;
                padding: $paginatorElementPadding;
                margin: $paginatorElementMargin;
                vertical-align: top;
                @include border-radius($paginatorElementBorderRadius);

                &.ui-state-active {
                    background-color: $highlightBgColor;
                    color: $highlightColorText;
                }

                &:not(.ui-state-active):hover {
                    background-color: $paginatorElementHoverBgColor;
                    color: $paginatorElementHoverIconColor;
                }
            }
        }

        .ui-dropdown {
            margin-left: $inlineSpacing;
            height: $paginatorElementHeight;
            min-width: auto;
        }
    }

    .p-datatable {
        .p-datatable-caption,
        .p-datatable-summary {
            background-color: $panelHeaderBgColor;
            color: $panelHeaderTextColor;
            border: $panelHeaderBorder;
            padding: $panelHeaderPadding;
            text-align: center;
        }

        .p-datatable-caption {
            border-bottom: 0 none;
            font-weight: $tableCaptionFontWeight;
        }

        .p-datatable-summary {
            border-top: 0 none;
            font-weight: $tableSummaryFontWeight;
        }

        .p-datatable-thead > tr > th {
            padding: $tableHeaderCellPadding;
            border: $tableHeaderCellBorder;
            font-weight: $tableHeaderCellFontWeight;
            color: $tableHeaderCellTextColor;
            background-color: $tableHeaderCellBgColor;
        }

        .p-datatable-tbody > tr > td {
            padding: $tableBodyCellPadding;
        }

        .p-datatable-tfoot > tr > td {
            padding: $tableFooterCellPadding;
            border: $tableFooterCellBorder;
            font-weight: $tableFooterCellFontWeight;
            color: $tableFooterCellTextColor;
            background-color: $tableFooterCellBgColor;
        }

        .ui-sortable-column {
            .ui-sortable-column-icon {
                color: $tableHeaderCellIconColor;
            }

            &:not(.p-highlight):hover {
                background-color: $tableHeaderCellHoverBgColor;
                color: $tableHeaderCellHoverTextColor;

                .ui-sortable-column-icon {
                    color: $tableHeaderCellHoverIconColor;
                }
            }

            &.p-highlight {
                background-color: $highlightBgColor;
                color: $highlightColorText;

                .ui-sortable-column-icon {
                    color: $highlightColorText;
                }
            }
        }

        .ui-editable-column {
            input {
                font-size: $fontSize;
                font-family: $fontFamily;

                &:focus {
                    outline: 1px solid $inputFocusBorderColor;
                    outline-offset: 2px;
                }
            }
        }

        .p-datatable-tbody {
            > tr {
                background-color: $tableBodyRowBgColor;
                color: $tableBodyRowTextColor;

                > td {
                    background-color: inherit;
                    border: $tableBodyCellBorder;
                }

                &.p-highlight {
                    background-color: $highlightBgColor;
                    color: $highlightColorText;

                    a {
                        color: $highlightColorText;
                    }
                }

                &.ui-contextmenu-selected {
                    background-color: $highlightBgColor;
                    color: $highlightColorText;
                }

                &.ui-table-dragpoint-top > td {
                    @include shadow(inset 0 2px 0 0 $highlightBgColor);
                }

                &.ui-table-dragpoint-bottom > td {
                    @include shadow(inset 0 -2px 0 0 $highlightBgColor);
                }
            }

            > tr:nth-child(even) {
                background-color: $tableBodyRowEvenBgColor;

                &.p-highlight {
                    background-color: $highlightBgColor;
                    color: $highlightColorText;

                    a {
                        color: $highlightColorText;
                    }
                }

                &.ui-contextmenu-selected {
                    background-color: $highlightBgColor;
                    color: $highlightColorText;
                }
            }
        }

        &.p-datatable-hoverable-rows {
            .ui-table-tbody > tr.ui-selectable-row:not(.p-highlight):not(.ui-contextmenu-selected):hover {
                cursor: pointer;
                background-color: $tableBodyRowHoverBgColor;
                color: $tableBodyRowHoverTextColor;
            }
        }

        .ui-column-resizer-helper {
            background-color: $tableResizerHelperBgColor;
        }
    }

    @media screen and (max-width: 40em) {
        .p-datatable {
            &.p-datatable-responsive {
                .ui-paginator-top {
                    border-bottom: $paginatorBorder;
                }

                .ui-paginator-bottom {
                    border-top: $paginatorBorder;
                }

                .p-datatable-tbody > tr > td {
                    border: 0 none;
                }
            }
        }
    }

    .ui-datagrid {
        .ui-datagrid-header {
            background-color: $panelHeaderBgColor;
            color: $panelHeaderTextColor;
            border: $panelHeaderBorder;
            padding: $panelHeaderPadding;
            font-weight: $panelHeaderFontWeight;
            border-bottom: 0 none;
        }

        .ui-datagrid-content {
            padding: $panelContentPadding;
            border: $panelContentBorder;
            background-color: $panelContentBgColor;
            color: $panelContentTextColor;
            padding: $panelContentPadding;
        }

        .ui-datagrid-footer {
            background-color: $panelFooterBgColor;
            color: $panelFooterTextColor;
            border: $panelFooterBorder;
            padding: $panelFooterPadding;
            border-top: 0 none;
        }
    }

    .ui-datalist {
        .ui-datalist-header {
            background-color: $panelHeaderBgColor;
            color: $panelHeaderTextColor;
            border: $panelHeaderBorder;
            padding: $panelHeaderPadding;
            font-weight: $panelHeaderFontWeight;
            border-bottom: 0 none;
        }

        .ui-datalist-content {
            padding: $panelContentPadding;
            border: $panelContentBorder;
            background-color: $panelContentBgColor;
            color: $panelContentTextColor;
            padding: $panelContentPadding;
        }

        .ui-datalist-footer {
            background-color: $panelFooterBgColor;
            color: $panelFooterTextColor;
            border: $panelFooterBorder;
            padding: $panelFooterPadding;
            border-top: 0 none;
        }
    }

    .ui-datascroller {
        .ui-datascroller-header {
            background-color: $panelHeaderBgColor;
            color: $panelHeaderTextColor;
            border: $panelHeaderBorder;
            padding: $panelHeaderPadding;
            font-weight: $panelHeaderFontWeight;
            border-bottom: 0 none;
        }

        .ui-datascroller-content {
            padding: $panelContentPadding;
            border: $panelContentBorder;
            background-color: $panelContentBgColor;
            color: $panelContentTextColor;
            padding: $panelContentPadding;
        }

        .ui-datascroller-footer {
            background-color: $panelFooterBgColor;
            color: $panelFooterTextColor;
            border: $panelFooterBorder;
            padding: $panelFooterPadding;
            border-top: 0 none;
        }
    }

    .ui-virtualscroller {
        .ui-virtualscroller-header {
            background-color: $panelHeaderBgColor;
            color: $panelHeaderTextColor;
            border: $panelHeaderBorder;
            padding: $panelHeaderPadding;
            font-weight: $panelHeaderFontWeight;
            border-bottom: 0 none;
        }

        .ui-virtualscroller-content {
            border: $panelContentBorder;
            background-color: $panelContentBgColor;
            color: $panelContentTextColor;
            .ui-virtualscroller-list {
                li {
                    border-bottom: $panelHeaderBorder;
                }
            }
        }

        .ui-virtualscroller-footer {
            background-color: $panelFooterBgColor;
            color: $panelFooterTextColor;
            border: $panelFooterBorder;
            padding: $panelFooterPadding;
            border-top: 0 none;
        }
    }

    .ui-dataview {
        .ui-dataview-header {
            background-color: $panelHeaderBgColor;
            color: $panelHeaderTextColor;
            border: $panelHeaderBorder;
            padding: $panelHeaderPadding;
            font-weight: $panelHeaderFontWeight;
            border-bottom: 0 none;
        }

        .ui-dataview-content {
            padding: $panelContentPadding;
            border: $panelContentBorder;
            background-color: $panelContentBgColor;
            color: $panelContentTextColor;
            padding: $panelContentPadding;
        }

        .ui-dataview-footer {
            background-color: $panelFooterBgColor;
            color: $panelFooterTextColor;
            border: $panelFooterBorder;
            padding: $panelFooterPadding;
            border-top: 0 none;
        }
    }

    .fc {
        th {
            background-color: $panelHeaderBgColor;
            border: $panelHeaderBorder;
            color: $panelHeaderTextColor;
            padding: $panelHeaderPadding;
        }

        td {
            &.p-component-content {
                background-color: $panelContentBgColor;
                border: $panelContentBorder;
                color: $panelContentTextColor;
            }

            &.fc-head-container {
                border: $panelHeaderBorder;
            }
        }

        .fc-row {
            border-right: $panelHeaderBorder;
        }

        .fc-event {
            background-color: $scheduleEventBgColor;
            border: $scheduleEventBorder;
            color: $scheduleEventTextColor;
        }

        .fc-toolbar {
            .fc-button {
                color: $buttonTextColor;
                background-color: $buttonBgColor;
                border: $buttonBorder;
                font-size: $fontSize;
                @include transition($buttonTransition);
                @include border-radius($borderRadius);

                &:enabled:hover {
                    background-color: $buttonHoverBgColor;
                    color: $buttonHoverTextColor;
                    border-color: $buttonHoverBorderColor;
                }

                .fc-icon-chevron-left{
                    font-family: 'PrimeIcons' !important;
                    text-indent: 0;
                    @include icon_override("\e900");
                }

                .fc-icon-chevron-right {
                    font-family: 'PrimeIcons' !important;
                    text-indent: 0;
                    @include icon_override("\e901");
                }

                &:focus {
                    outline: $buttonFocusOutline;
                    outline-offset: $buttonFocusOutlineOffset;
                    @include shadow($buttonFocusShadow);
                }
            }

            .fc-button-group {
                .fc-button {
                    @include border-radius(0);

                    &:first-child {
                        @include border-radius-left($borderRadius);
                    }

                    &:last-child {
                        @include border-radius-right($borderRadius);
                    }
                }
            }
        }

        .fc-divider {
            background-color: $panelHeaderBgColor;
            border: $panelHeaderBorder;
        }
    }

    .ui-fluid {
        .fc {
            .fc-toolbar {
                .fc-button {
                    width: auto;
                }
            }
        }
    }

    .ui-picklist {
        .ui-picklist-buttons {
            button {
                font-size: $fontSize + 2;
            }
        }

        .ui-picklist-caption {
            background-color: $panelHeaderBgColor;
            color: $panelHeaderTextColor;
            border: $panelHeaderBorder;
            padding: $panelHeaderPadding;
            font-weight: $panelHeaderFontWeight;
            border-bottom: 0 none;
            @include border-radius-top($borderRadius);
        }

        .ui-picklist-filter-container {
            padding: $inputListHeaderPaddingTop $inputListHeaderPaddingRight $inputListHeaderPaddingBottom $inputListHeaderPaddingLeft;
            border: $panelHeaderBorder;
            color: $inputListHeaderTextColor;
            background-color: $inputListHeaderBgColor;
            margin: $inputListHeaderMargin;
            border-bottom: 0 none;

            input.ui-picklist-filter {
                width: 100%;
                padding-right: 2em;
                text-indent: 0;
            }

            .ui-picklist-filter-icon {
                top: 50%;
                margin-top: -.5em;
                left: auto;
                right: $inputListHeaderPaddingRight + .5em;
                color: $inputListHeaderSearchIconColor;
            }
        }

        .ui-picklist-buttons {
            padding: $panelContentPadding;
        }

        .ui-picklist-list {
            border: $panelContentBorder;
            background-color: $panelContentBgColor;
            color: $panelContentTextColor;
            padding: $inputListPadding;

            .ui-picklist-item {
                padding: $inputListItemPadding;
                margin: $inputListItemMargin;
                border: $inputListItemBorder;
                color: $inputListItemTextColor;
                background-color: $inputListItemBgColor;

                &:not(.p-highlight):hover {
                    background-color: $inputListItemHoverBgColor;
                    color: $inputListItemHoverTextColor;
                }

                &.p-highlight {
                    background-color: $inputListItemHighlightBgColor;
                    color: $inputListItemHighlightTextColor;
                }
            }

            .ui-picklist-droppoint-highlight {
                background-color: $highlightBgColor;
            }
        }
    }

    @media (max-width: 40em) {
        .ui-picklist.ui-picklist-responsive {
            .ui-picklist-buttons {
                padding: $panelContentPadding;
            }
        }
    }

    .ui-orderlist {
        .ui-orderlist-controls {
            padding: $panelContentPadding;

            button {
                font-size: $fontSize + 2;
            }
        }

        .ui-orderlist-caption {
            background-color: $panelHeaderBgColor;
            color: $panelHeaderTextColor;
            border: $panelHeaderBorder;
            padding: $panelHeaderPadding;
            font-weight: $panelHeaderFontWeight;
            border-bottom: 0 none;
            @include border-radius-top($borderRadius);
        }

        .ui-orderlist-filter-container {
            padding: $inputListHeaderPaddingTop $inputListHeaderPaddingRight $inputListHeaderPaddingBottom $inputListHeaderPaddingLeft;
            border: $panelHeaderBorder;
            color: $inputListHeaderTextColor;
            background-color: $inputListHeaderBgColor;
            margin: $inputListHeaderMargin;
            border-bottom: 0 none;

            input.p-inputtext {
                width: 100%;
                padding-right: 2em;
                text-indent: 0;
            }

            .ui-orderlist-filter-icon {
                top: 50%;
                margin-top: -.5em;
                left: auto;
                right: $inputListHeaderPaddingRight + .5em;
                color: $inputListHeaderSearchIconColor;
            }
        }

        .ui-orderlist-list {
            border: $panelContentBorder;
            background-color: $panelContentBgColor;
            color: $panelContentTextColor;
            padding: $inputListPadding;

            .ui-orderlist-item {
                padding: $inputListItemPadding;
                margin: $inputListItemMargin;
                border: $inputListItemBorder;
                color: $inputListItemTextColor;
                background-color: $inputListItemBgColor;

                &:not(.p-highlight):hover {
                    background-color: $inputListItemHoverBgColor;
                    color: $inputListItemHoverTextColor;
                }

                &.p-highlight {
                    background-color: $inputListItemHighlightBgColor;
                    color: $inputListItemHighlightTextColor;
                }
            }

            .ui-orderlist-droppoint-highlight {
                background-color: $highlightBgColor;
            }
        }
    }

    .ui-tree {
        border: $panelContentBorder;
        background-color: $panelContentBgColor;
        color: $panelContentTextColor;
        padding: $panelContentPadding;

        .ui-tree-container {
            padding: 0.286em;
            margin: 0;

            .ui-treenode {
                padding: $treeNodePadding;

                .ui-treenode-content {
                    padding: 0;
                    border: 1px solid transparent;

                    .ui-tree-toggler {
                        vertical-align: middle;
                        display: inline-block;
                        float: none;
                        margin: 0 $treeNodeContentSpacing 0 0;
                        color: $dataIconColor;
                    }

                    .ui-treenode-icon {
                        vertical-align: middle;
                        display: inline-block;
                        margin: 0 $treeNodeContentSpacing 0 0;
                        color: $dataIconColor;
                    }

                    .ui-treenode-label {
                        margin: 0;
                        vertical-align: middle;
                        display: inline-block;
                        padding: $treeNodeLabelPadding;

                        &.p-highlight {
                            background-color: $highlightBgColor;
                            color: $highlightColorText;
                        }
                    }

                    .p-checkbox {
                        margin: 0 $inlineSpacing 0 0;

                        .p-checkbox-icon {
                            margin: 1px 0 0 0;
                        }
                    }

                    &.ui-treenode-selectable {
                        .ui-treenode-label:not(.p-highlight):hover {
                            background-color: $inputListItemHoverBgColor;
                            color: $inputListItemHoverTextColor;
                        }
                    }

                    > span {
                        line-height: inherit;
                    }

                    &.ui-treenode-dragover {
                        background-color: $inputListItemHoverBgColor;
                        color: $inputListItemHoverTextColor;
                    }
                }
            }

            .ui-treenode-droppoint {
                &.ui-treenode-droppoint-active {
                    background-color: $highlightBgColor;
                }
            }
        }

        &.ui-tree-horizontal {
            padding-left: 0;
            padding-right: 0;

            .ui-treenode {

                .ui-treenode-content {
                    border: $panelContentBorder;
                    background-color: $panelContentBgColor;
                    color: $panelContentTextColor;
                    padding: $panelContentPadding;

                    &.p-highlight {
                        background-color: $highlightBgColor;
                        color: $highlightColorText;
                    }

                    .p-checkbox {
                        .ui-icon {
                            color: $primaryColor;
                        }
                    }

                    .ui-treenode-label:not(.p-highlight):hover {
                        background-color: inherit;
                        color: inherit;
                    }

                    &.ui-treenode-selectable:not(.p-highlight):hover {
                        background-color: $inputListItemHoverBgColor;
                        color: $inputListItemHoverTextColor;
                    }
                }
            }
        }

        .ui-tree-filter-container {
            .ui-tree-filter {
                width: 100%;
                padding-right: 2em;
            }

            .ui-tree-filter-icon {
                top: 50%;
                left: auto;
                margin-top: -.5em;
                right: .8em;
                color: $inputListHeaderSearchIconColor;
            }
        }
    }

    .ui-organizationchart {
        .ui-organizationchart-node-content {
            &.ui-organizationchart-selectable-node:not(.p-highlight):hover {
                background-color: $inputListItemHoverBgColor;
                color: $inputListItemHoverTextColor;
            }

            &.p-highlight {
                background-color: $highlightBgColor;
                color: $highlightColorText;

                .ui-node-toggler {
                    i {
                        color: darken($highlightBgColor, 25%);
                    }
                }
            }
        }

        .ui-organizationchart-line-down {
            background-color: $organizationChartConnectorColor;
        }

        .ui-organizationchart-line-left {
            border-right: $panelContentBorder;
            border-color: $organizationChartConnectorColor;
        }

        .ui-organizationchart-line-top {
            border-top: $panelContentBorder;
            border-color: $organizationChartConnectorColor;
        }

        .ui-organizationchart-node-content {
            border: $panelContentBorder;
            background-color: $panelContentBgColor;
            color: $panelContentTextColor;
            padding: $panelContentPadding;
        }

        .ui-organizationchart-node-content .ui-node-toggler {
            bottom: -.7em;
            margin-left: -.46em;
            color: $dataIconColor;
        }
    }

    .p-treetable {
        .p-treetable-caption,
        .p-treetable-summary {
            background-color: $panelHeaderBgColor;
            color: $panelHeaderTextColor;
            border: $panelHeaderBorder;
            padding: $panelHeaderPadding;
            text-align: center;
        }

        .p-treetable-caption {
            border-bottom: 0 none;
            font-weight: $tableCaptionFontWeight;
        }

        .p-treetable-summary {
            border-top: 0 none;
            font-weight: $tableSummaryFontWeight;
        }

        .p-treetable-thead > tr > th {
            padding: $tableHeaderCellPadding;
            border: $tableHeaderCellBorder;
            font-weight: $tableHeaderCellFontWeight;
            color: $tableHeaderCellTextColor;
            background-color: $tableHeaderCellBgColor;
        }

        .p-treetable-tbody > tr > td {
            padding: $tableBodyCellPadding;
        }

        .p-treetable-tfoot > tr > td {
            padding: $tableFooterCellPadding;
            border: $tableFooterCellBorder;
            font-weight: $tableFooterCellFontWeight;
            color: $tableFooterCellTextColor;
            background-color: $tableFooterCellBgColor;
        }

        .ui-sortable-column {
            .ui-sortable-column-icon {
                color: $tableHeaderCellIconColor;
            }

            &:not(.p-highlight):hover {
                background-color: $tableHeaderCellHoverBgColor;
                color: $tableHeaderCellHoverTextColor;

                .ui-sortable-column-icon {
                    color: $tableHeaderCellHoverIconColor;
                }
            }

            &.p-highlight {
                background-color: $highlightBgColor;
                color: $highlightColorText;

                .ui-sortable-column-icon {
                    color: $highlightColorText;
                }
            }
        }

        .ui-editable-column {
            input {
                font-size: $fontSize;
                font-family: $fontFamily;

                &:focus {
                    outline: 1px solid $inputFocusBorderColor;
                    outline-offset: 2px;
                }
            }
        }

        .p-treetable-tbody {
            > tr {
                background-color: $tableBodyRowBgColor;
                color: $tableBodyRowTextColor;

                > td {
                    background-color: inherit;
                    border: $tableBodyCellBorder;

                    .p-treetable-toggler {
                        color: $dataIconColor;
                        vertical-align: middle;
                    }

                    .p-treetable-chkbox {
                        vertical-align: middle;
                        margin-right: $inlineSpacing;
                    }
                }

                &.p-highlight {
                    background-color: $highlightBgColor;
                    color: $highlightColorText;

                    > td {
                        background-color: inherit;
                        border: $tableBodyCellBorder;

                        .p-treetable-toggler {
                            color: $highlightColorText;
                        }
                    }
                }

                &.ui-contextmenu-selected {
                    background-color: $highlightBgColor;
                    color: $highlightColorText;

                    > td {
                        background-color: inherit;
                        border: $tableBodyCellBorder;

                        .p-treetable-toggler {
                            color: $highlightColorText;
                        }
                    }
                }
            }
        }

        .p-column-resizer-helper {
            background-color: $tableResizerHelperBgColor;
        }
    }

    .ui-carousel {
        .ui-carousel-content {
            .ui-carousel-prev,
            .ui-carousel-next {
                background-color: $carouselNavButtonsBgColor;
                border: $carouselNavButtonsBorder;
                @include border-radius($carouselNavButtonsBorderRadius);
                margin: $carouselNavButtonsMargin;
                color: $carouselNavButtonsColor;
                @include transition($carouselNavButtonsTransition);

                &:not(.p-disabled) {
                    &:hover {
                        background-color: $carouselNavButtonsHoverBgColor;
                        color: $carouselNavButtonsHoverColor;
                        border-color: $carouselNavButtonsHoverBorderColor;
                    }
                }
            }
        }

        .ui-carousel-dots-container {
            .ui-carousel-dot-item {
                > .p-button {
                    border-color: transparent;
                    background-color: transparent;
                }

                .ui-carousel-dot-icon {
                    width: $carouselDotIconWidth;
                    height: $carouselDotIconHeight;
                    background-color: $carouselDotIconBgColor;
                    margin: $carouselDotIconMargin;

                    &::before {
                        content: ' ';
                    }
                }

                &.p-highlight {
                    .ui-carousel-dot-icon {
                        background-color: $carouselActiveDotIconBgColor;
                    }
                }
            }
        }
    }
}
