@mixin overlay-content-box {
    padding: 0 20px;
    margin: 20px 0;
}

body {
    .p-dialog {
        background: $nokiaUiWhiteColor;
        border-radius: $nokiaDefaultBorderRadius;
        box-shadow: 0 24px 24px rgba(0, 0, 0, 0.3);

        .p-dialog-titlebar {
            padding: 0 20px;
            margin-top: 20px;
        }

        .p-dialog-header {
            padding: 0 20px;
            margin-top: 20px;
            margin-bottom: 20px;
            border: none;
            background: transparent;
            color: $nokiaUiBlackColor;
            font-size: $nokiaHeaderFontSize;
            line-height: 21px;
            white-space: normal;

            .p-dialog-header-icons {
                display: none;
            }
        }

        .p-dialog-content {
            height: 100%;
            padding: 20px 20px 30px 20px;
            border: none;
            color: $nokiaGray4Color;
            background: transparent;
        }

        .p-dialog-footer { // for overlay
            top: 0;
            border: none;
            border-top: solid 1px $nokiaGray2Color;
            background: transparent;
            padding: 20px;
            white-space: nowrap;

            button {
                margin: 0 0 0 8px;
                min-width: $nokiaPrimiaryButtonMinWidth;

                .p-button-icon-left {
                    display: none;
                }

                .p-button-text {
                    padding-left: 1.545em;
                }
            }
        }

        &.p-confirm-dialog {
            .p-dialog-content {
                padding: 3px 20px;
                margin-bottom: 30px;
                align-items: flex-start;

                .p-confirm-dialog-icon.confirm-icon {
                    font-size: 22px;
                    margin-right: 6px;
                    margin-top: -8px;

                    &.mdi-alert {
                        color: $nokiaAlarmRedColor;
                        font-size: 24px;
                    }

                    &.mdi-alert-circle {
                        color: $nokiaUiWarningYellowColor;
                    }
                }

                .p-confirm-dialog-message {
                    line-height: 16px;
                }
            }

            .p-dialog-footer {
                display: flex;
                justify-content: flex-start;
                flex-direction: row-reverse;

                .p-button.p-confirm-dialog-accept {
                    background-color: $nokiaDarkBlueColor;
                    color: $nokiaUiWhiteColor;
                    box-shadow: $nokiaDefaultBoxShadow;
                    border: 1px solid transparent;

                    &:enabled {
                        &:focus {
                            background-color: $nokiaDarkBlueColor;
                            color: $nokiaUiWhiteColor;
                            border: 1px solid $nokiaDarkBlueColor;
                            border-radius: 3px;

                            &::after {
                                content: '';
                                display: block;
                                position: absolute;
                                top: 0;
                                bottom: 0;
                                left: 0;
                                right: 0;
                                border-radius: 3px;
                                border: 1px solid $nokiaUiWhiteColor;
                            }
                        }

                        &:hover {
                            background-color: $nokiaDarkerBlueColor;
                            box-shadow: $nokiaDefaultBoxShadow;
                            color: $nokiaUiWhiteColor;
                        }

                        &:active {
                            background-color: $nokiaDarkerBlueColor;
                            box-shadow: $nokiaActiveBoxShadow;
                            color: $nokiaUiWhiteColor;
                        }
                    }

                    &:disabled {
                        background-color: $nokiaGray7Color;
                    }
                }
            }
        }
    }

    .ui-message-dialog-info,
    .ui-message-dialog-warning,
    .ui-message-dialog-error {
        .p-dialog-title::before {
            content: ' ';
            display: inline-block;
            vertical-align: top;
            margin-right: 0.5em;
            width: 20px;
            height: 20px;
            background-size: 20px;
        }
    }

    .ui-message-dialog-info .p-dialog-title::before {
        /* https://github.com/FortAwesome/Font-Awesome-Pro/blob/master/advanced-options/raw-svg/regular/info-circle.svg */
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 512 512' width='512' height='512'%3E%3Cpath fill='rgb(0,161,204)' d='M256 8C119.043 8 8 119.083 8 256c0 136.997 111.043 248 248 248s248-111.003 248-248C504 119.083 392.957 8 256 8zm0 110c23.196 0 42 18.804 42 42s-18.804 42-42 42-42-18.804-42-42 18.804-42 42-42zm56 254c0 6.627-5.373 12-12 12h-88c-6.627 0-12-5.373-12-12v-24c0-6.627 5.373-12 12-12h12v-64h-12c-6.627 0-12-5.373-12-12v-24c0-6.627 5.373-12 12-12h64c6.627 0 12 5.373 12 12v100h12c6.627 0 12 5.373 12 12v24z'/%3E%3C/svg%3E");
    }

    .ui-message-dialog-warning .p-dialog-title::before {
        ///* https://github.com/FortAwesome/Font-Awesome-Pro/blob/master/advanced-options/raw-svg/regular/exclamation-triangle.svg */
        height: 18px;
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 576 512' width='576' height='512'%3E%3Cpath fill='rgb(255,204,0)' d='M569.517 440.013C587.975 472.007 564.806 512 527.94 512H48.054c-36.937 0-59.999-40.055-41.577-71.987L246.423 23.985c18.467-32.009 64.72-31.951 83.154 0l239.94 416.028zM288 354c-25.405 0-46 20.595-46 46s20.595 46 46 46 46-20.595 46-46-20.595-46-46-46zm-43.673-165.346l7.418 136c.347 6.364 5.609 11.346 11.982 11.346h48.546c6.373 0 11.635-4.982 11.982-11.346l7.418-136c.375-6.874-5.098-12.654-11.982-12.654h-63.383c-6.884 0-12.356 5.78-11.981 12.654z'/%3E%3C/svg%3E");
    }

    .ui-message-dialog-error .p-dialog-title::before {
        ///* https://github.com/FortAwesome/Font-Awesome-Pro/blob/master/advanced-options/raw-svg/regular/times-circle.svg */
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 512 512' width='512' height='512'%3E%3Cpath fill='rgb(218,25,20)' d='M256 8C119 8 8 119 8 256s111 248 248 248 248-111 248-248S393 8 256 8zm121.6 313.1c4.7 4.7 4.7 12.3 0 17L338 377.6c-4.7 4.7-12.3 4.7-17 0L256 312l-65.1 65.6c-4.7 4.7-12.3 4.7-17 0L134.4 338c-4.7-4.7-4.7-12.3 0-17l65.6-65-65.6-65.1c-4.7-4.7-4.7-12.3 0-17l39.6-39.6c4.7-4.7 12.3-4.7 17 0l65 65.7 65.1-65.6c4.7-4.7 12.3-4.7 17 0l39.6 39.6c4.7 4.7 4.7 12.3 0 17L312 256l65.6 65.1z'/%3E%3C/svg%3E");
    }

    .p-overlaypanel {
        border: none;
        box-shadow: 0 7px 13px rgba(0, 0, 0, 0.3), 0 -10px 13px -13px rgba(0, 0, 0, 0.3);

        @include border-radius($nokiaDefaultBorderRadius);

        &::before {
            display: none;
        }

        .p-dialog-header {
            margin: 18px 0 10px;
            color: $nokiaUiGray4Color;
            font-size: 11px;
            line-height: 14px;
        }

        .p-overlaypanel-content {
            padding: 20px;

            .p-dialog-content {
                padding: 20px;
            }
        }

        .p-dialog-footer {
            top: 0;
            border: none;
            border-top: solid 1px $nokiaGray2Color;
            background: transparent;
            padding: 20px;
            white-space: nowrap;
            text-align: right;

            button {
                margin: 0 0 0 8px;
                min-width: $nokiaPrimiaryButtonMinWidth;

                .p-button-icon-left {
                    display: none;
                }

                .p-button-text {
                    padding-left: 1.545em;
                }
            }
        }
    }

    .p-overlaypanel-content > .p-dialog {
        // This fixes attaching overlayPanel to target, when styled as dialog.
        position: static;
        margin: -20px;
        box-shadow: none;
    }
}
