@import '../nokia-variables';

body {
    .p-radiobutton {
        width: $nokiaRadioButtonHeight;
        height: $nokiaRadioButtonHeight;
        position: relative;

        &.p-radiobutton-checked .p-radiobutton-box {
            background-color: $nokiaDarkBlueColor;
            border-color: $nokiaDarkBlueColor;

            &.p-disabled {
                background-color: $nokiaGray11Color;
            }

            .p-radiobutton-icon {
                border: 2px solid $nokiaUiWhiteColor;
            }

            &:not(.p-disabled):hover {
                background-color: $nokiaToastBlueColor;
                border-color: $nokiaToastBlueColor;
                box-shadow: $nokiaDefaultBoxShadow;
            }

            &.p-focus {
                background-color: $nokiaToastBlueColor;
                border-color: $nokiaToastBlueColor;
                box-shadow: $nokiaDefaultBoxShadow;

                .p-radiobutton-icon::after {
                    display: block;
                    content: '';
                    width: calc(#{$nokiaRadioButtonHeight} - 2px);
                    height: calc(#{$nokiaRadioButtonHeight} - 2px);
                    border: 1px solid $nokiaUiWhiteColor;
                    border-radius: 50%;
                    margin: -5px -5px;
                }
            }
        }

        .p-radiobutton-checked .p-disabled .p-radiobutton-box {
            background-color: $nokiaGray11Color;
            border-color: $nokiaGray2Color;
        }

        .p-radiobutton-box {
            width: $nokiaRadioButtonHeight;
            height: $nokiaRadioButtonHeight;
            color: $nokiaUiWhiteColor;
            background-color: $nokiaUiWhiteColor;
            border: 1px solid $nokiaGray3Color;

            &.p-focus {
                background-color: $nokiaUiWhiteColor;
                border-color: $nokiaUiGray4Color;
                box-shadow: none;
            }

            .p-radiobutton-icon {
                width: $nokiaRadioButtonHeight / 2;
                height: $nokiaRadioButtonHeight / 2;
                margin-left: -1 * $nokiaRadioButtonHeight / 4;
                margin-top: -1 * $nokiaRadioButtonHeight / 4;
                background-color: $nokiaUiWhiteColor;
            }

            &.p-disabled {
                background-color: $nokiaUiBackgroundColor;
                border-color: $nokiaGray2Color;
            }
        }
    }

    .p-radiobutton-label {
        font-size: $nokiaDefaultTextFontSize;
        color: $nokiaGray4Color;
        cursor: pointer;
    }

    .p-radiobutton-label.p-disabled {
        color: $nokiaGray2Color;
    }
}
