body .ui-inputgroup {
    .p-inputtext:not(:first-child) {
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
        border-left-width: 0;
    }

    .disabled {
        background-color: #F6F8FA;
        border-color: #BEC8D2 !important;
        color: black;
        opacity: 0.5;
    }

    &:hover {
        .ui-inputgroup-addon {
            &:hover {
                border-color: $nokiaGray3Color;
            }

            background-color: $nokiaUiHoverBgBlueColor;
            border-color: $nokiaLightBlueColor;
            transition: border-color 0.2s;
        }
    }

    &:focus-within {
        .ui-inputgroup-addon {
            &:hover {
                border-color: $nokiaUiBlackColor;
                transition: border-color 0.2s;
            }

            background-color: $nokiaUiWhiteColor;
            border-color: $nokiaUiBlackColor;
        }
    }
}


body .ui-inputgroup .ui-inputgroup-addon {
    border-style: solid;
    border-width: 1px;
    padding: 0.2em 0.1em 0.2em 0.1em;
    height: 26px;
    background-color: #FFFFFF;
    color: $nokiaUiGray4Color;
    border-color: $nokiaGray3Color;
    cursor: pointer;
    min-width: 24px;
    transition: border-color 0.2s;

    div {
        width: 100%;
        height: 100%;
        border-radius: 2px;
        margin-left: 1px;
    }

    i {
        font-size: 16px;
        line-height: 20px;
        width: 100%;
        display: inline-flex;
        justify-content: center;
    }

    &:first-child:not(.clear-button) {
        @include border-radius-left($borderRadius);

        border-right: none;
        margin-right: -1px;

        &:hover {
            background-color: $nokiaUiLightBlue1Color;
        }
    }

    &:last-child {
        @include border-radius-right($borderRadius);

        &:hover {
            background-color: $nokiaUiLightBlue1Color;
        }

        &.clear-button {
            @include border-radius-left(0);
        }
    }
}

body .p-dropdown {
    .p-dropdown-label {
        display: flex;
        align-items: center;
    }
}

/* Validations */
.p-inputtext.ng-dirty.ng-invalid,
p-dropdown.ng-dirty.ng-invalid > .p-dropdown,
p-autocomplete.ng-dirty.ng-invalid > .ui-autocomplete > .p-inputtext,
p-calendar.ng-dirty.ng-invalid > .ui-calendar > .p-inputtext,
p-chips.ng-dirty.ng-invalid > .p-inputtext,
p-inputmask.ng-dirty.ng-invalid > .p-inputtext,
p-checkbox.ng-dirty.ng-invalid .p-checkbox-box,
p-radiobutton.ng-dirty.ng-invalid .ui-radiobutton-box,
p-inputswitch.ng-dirty.ng-invalid .ui-inputswitch,
p-listbox.ng-dirty.ng-invalid .p-inputtext,
p-multiselect.ng-dirty.ng-invalid > .p-multiselect,
p-spinner.ng-dirty.ng-invalid > .p-inputtext,
p-selectbutton.ng-dirty.ng-invalid .ui-button,
p-togglebutton.ng-dirty.ng-invalid .p-button {
    border: 1px solid $nokiaGray3Color !important;
}
