@import '../_nokia-variables.scss';

body {
    .p-inputtext, .ui-inputtext {
        height: $nokiaControlDefaultHeight;
        font-family: $nokiaDefaultFontFace;
        font-size: $nokiaControlTextFontSize;
        color: $nokiaUiGray4Color;
        background-color: $nokiaUiWhiteColor;
        border: 1px solid $nokiaGray3Color;
        border-radius: 3px;
        margin: 0;

        &:enabled {
            &:hover:not(.ui-state-error) {
                background-color: $nokiaUiHoverBgBlueColor;
                border-color: $nokiaLightBlueColor;
            }

            &:focus:not(.ui-state-error) {
                background-color: $nokiaUiWhiteColor;
                border-color: $nokiaUiBlackColor;
            }
        }

        &:disabled {
            color: $nokiaGray3Color;
            background-color: $nokiaUiWhiteColor;
            border-color: $nokiaGray2Color;
            opacity: 1;
        }
    }

    table {
        .p-inputtext {
            height: $nokiaControlInTableDefaultHeight;
        }
    }
}
