@import '../nokia-variables';

body {
    .p-chips {
        height: $nokiaChipsHeight;
        margin: 0;

        > ul.p-inputtext {
            border-color: $nokiaDarkBlueColor;
            border-radius: 15px;
            line-height: 1em;
            padding: 0;

            &:not(.p-disabled):hover {
                background-color: $nokiaUiHoverBgBlueColor;
                border-color: $nokiaLightBlueColor;
            }

            &:not(.p-disabled).p-focus {
                background-color: $nokiaUiWhiteColor;
                border-color: $nokiaUiGray4Color;

                .p-chips-token-icon {
                    background-color: $nokiaUiGray4Color;
                    border-radius: calc(#{$nokiaChipsIconHeight} / 2);
                }
            }

            .p-chips-input-token {
                width: 10px;
            }

            .p-chips-token {
                background-color: transparent;
                color: black;
                margin: 2px 0 2px 0;
                padding-left: 10px;
                padding-right: $nokiaChipsIconHeight;
                position: relative;

                .p-chips-token-label {
                    font-size: $nokiaControlTextFontSize;
                    margin-right: 8px;
                }

                .p-chips-token-icon {
                    font-size: $nokiaDefaultTextFontSize;
                    width: $nokiaChipsIconHeight;
                    height: $nokiaChipsIconHeight;
                    background-color: $nokiaDarkBlueColor;
                    border-radius: calc(#{$nokiaChipsIconHeight} / 2);
                    color: white;
                    right: 0;
                    position: absolute;
                    top: 50%;

                    &:hover {
                        background-color: $nokiaLightBlueColor;
                    }
                }
            }

            &.p-disabled {
                border-color: $nokiaGray2Color;

                .p-chips-token-label::after {
                    width: $nokiaChipsIconHeight;
                    height: $nokiaChipsIconHeight;
                    font-family: 'PrimeIcons', sans-serif;
                    font-size: $nokiaDefaultTextFontSize;
                    content: $nokiaPrimeTimesIconContent;
                    border-radius: calc(#{$nokiaChipsIconHeight} / 2);
                    background-color: $nokiaGray2Color;
                    color: $nokiaUiWhiteColor;
                    position: absolute;
                    padding-top: 1px;
                    right: 0;
                }
            }
        }
    }
}
