@import 'theme/nokia-variables';

.ui-badge {
    background-color: $nokiaGray4Color;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    color: $nokiaUiWhiteColor;
    font-size: 8px;
    border-radius: $nokiaDefaultBorderRadius;
    height: 14px;
    padding: 0 5px;
    text-transform: capitalize;

    &.ui-badge-white {
        background-color: $nokiaUiWhiteColor;
        border: 1px solid $nokiaGray4Color;
        color: $nokiaUiGray4Color;
    }
}
