@import '../nokia-variables';

body {
    p-multiSelect {
        width: 100%;
        display: block;
    }

    .p-multiselect {
        width: 100%;
        height: $nokiaControlDefaultHeight;
        background-color: $nokiaUiWhiteColor;
        border: 1px solid $nokiaGray3Color;
        color: $nokiaUiGray4Color;
        margin: 0;
        border-radius: 3px;

        &.p-disabled {
            color: $nokiaGray2Color;
            background-color: $nokiaUiWhiteColor;
            border-color: $nokiaGray2Color;

            .p-multiselect-label {
                color: $nokiaGray2Color;
            }

            .p-multiselect-trigger {
                color: $nokiaGray2Color;
                border-left: 1px solid $nokiaGray2Color;
            }
        }

        &:not(.p-disabled):hover {
            &:not(.p-multiselect-open) {
                background-color: $nokiaUiHoverBgBlueColor;
                border-color: $nokiaLightBlueColor;

                .p-multiselect-trigger {
                    border-color: $nokiaLightBlueColor;
                }
            }
        }

        &:not(.p-disabled):focus-within {
            background-color: $nokiaUiWhiteColor;
            border-color: $nokiaUiGray4Color;

            .p-multiselect-trigger {
                border-color: $nokiaUiGray4Color;
            }
        }

        .p-multiselect-label {
            font-size: $nokiaControlTextFontSize;
            padding-left: 0.73em;
            padding-right: 2.73em;
        }

        .p-multiselect-trigger {
            border-left: 1px solid $nokiaGray3Color;
            color: $nokiaUiGray4Color;
            width: 26px;
            border-bottom-right-radius: 3px;
            height: 100%;
            border-top-right-radius: 3px;
        }
    }

    .p-multiselect-secondary {
        .p-multiselect {
            background-color: transparent;
            border: 1px solid transparent;

            .p-multiselect-trigger {
                border: none;
            }

            &:not(.p-disabled):hover {
                &:not(.p-multiselect-open) {
                    background-color: $nokiaUiLightBlue1Color;
                    border-color: $nokiaUiLightBlue1Color;

                    .p-multiselect-trigger {
                        background-color: $nokiaUiLightBlue1Color;
                        border-color: $nokiaUiLightBlue1Color;
                    }
                }
            }

            &:not(.p-disabled):focus-within {
                background-color: transparent;
                border-color: $nokiaUiGray4Color;

                .p-multiselect-trigger {
                    border-color: $nokiaUiGray4Color;
                }
            }
        }

        .p-multiselect-panel {
            .p-multiselect-header {
                padding: 0 30px 0 8px;

                &::before {
                    display: none;
                }

                .p-multiselect-close {
                    display: block;

                    .pi-times {
                        font-size: 1em;

                        &::before {
                            color: $nokiaUiWhiteColor;
                            background-color: $nokiaGray3Color;
                            border-color: $nokiaGray3Color;
                            border-radius: 50%;
                        }

                        &:hover {
                            &::before {
                                background-color: $nokiaLightBlueColor;
                                border-color: $nokiaLightBlueColor;
                            }
                        }
                    }
                }
            }
        }
    }

    .p-multiselect-panel {
        padding: 0;

        .p-multiselect-header {
            height: $nokiaControlDefaultHeight;
            padding: 0 30px 0 25px;

            &::before {
                font: normal normal normal 24px/1 'Material Design Icons', sans-serif;
                font-size: 1.5em;
                position: absolute;
                top: 5px;
                left: 5px;
                content: $nokiaMDIFilterIconContent;
                color: $nokiaUiDefaultTextColor;
            }

            .p-multiselect-filter-container {
                width: 100%;

                .p-inputtext {
                    height: $nokiaControlDefaultHeight;
                    font-size: 11px;
                    background-color: transparent;
                    border: none;
                    padding: 0;
                    margin: 0;
                }

                .p-multiselect-filter-icon {
                    display: none;
                }
            }

            .p-checkbox {
                display: none;
            }

            .p-multiselect-close {
                display: none;
            }
        }

        .p-multiselect-items {
            padding: 0;

            .p-multiselect-item {
                font-size: 11px;
                padding: 0.2em 8px;

                &:focus {
                    outline: none;
                }

                &.p-highlight {
                    background-color: $nokiaUiWhiteColor;
                    color: $nokiaUiGray4Color;

                    &.p-disabled {
                        color: $nokiaGray2Color;

                        .p-checkbox-box:hover {
                            border-color: $nokiaGray11Color;
                        }
                    }
                }

                &:not(.p-highlight):not(.p-disabled):hover {
                    background-color: $nokiaUiLightBlue1Color;
                }

                &.p-disabled {
                    color: $nokiaGray2Color;

                    .p-checkbox-box {
                        border-color: $nokiaGray11Color;

                        .p-checkbox-icon {
                            background-color: $nokiaGray11Color;
                            border-color: $nokiaGray11Color;
                        }
                    }
                }
            }
        }
    }

    table {
        .p-multiselect {
            height: 22px;

            .p-multiselect-label-container {
                .p-multiselect-label {
                    float: left;
                    height: 22px;
                    padding-left: 8px;
                    padding-top: 0.3em;
                }
            }
        }
    }

    p-multiselectitem {
        display: block;
        margin-bottom: 6px;

        &:focus {
            background-color: yellow;
        }

        &:hover .p-checkbox-box.p-highlight:not(.p-disabled) {
            background-color: $nokiaDarkerBlueColor;
            border-color: $nokiaDarkerBlueColor;

            .p-checkbox-icon {
                background-color: $nokiaDarkerBlueColor;
                border: $nokiaDarkerBlueColor;
            }
        }

        &:hover .p-checkbox-box:not(.p-disabled) {
            background-color: $powerOffBackgroundColor;
            border-color: $nokiaLightBlueColor;
            box-shadow: none;
        }

        &:active .p-checkbox-box.ui-state-active:not(.p-disabled) {
            border-color: $nokiaDarkerBlueColor;
            box-shadow: $nokiaActiveBoxShadow;
        }

        &:active .p-checkbox-box:not(.p-disabled) {
            border: 1px solid $nokiaLightBlueColor;
            box-shadow: $nokiaActiveBoxShadow;
        }
    }
}
