@import '../nokia-variables';

$toastDefaultPadding: 15px;
$toastAlternativePadding1: 10px;
$toastAlternativePadding2: 20px;
$toastAlternativePadding3: 22px;

@mixin toast-override($mainPadding, $textMarginLeft, $bgPosition, $lineGap) {
    padding: $mainPadding;
    background-position: $bgPosition;

    .toast-title {
        margin-left: $textMarginLeft;
    }

    .toast-message {
        margin-left: $textMarginLeft;
        margin-top: $lineGap;
    }
}

.toast-container .ngx-toastr {
    &.toastr-type-1 {
        @include toast-override(
            15px,
            32px,
            15px 10px,
            5px
        );
    }

    &.toastr-type-2 {
        @include toast-override(
            22px 15px,
            32px,
            15px 17px,
            5px
        );
    }

    &.toastr-type-3 {
        @include toast-override(
            15px,
            32px,
            15px 5px,
            5px
        );
    }

    &.toastr-type-4 {
        @include toast-override(
            22px 15px,
            32px,
            15px 12px,
            5px
        );
    }
}
