@import 'theme/nokia-variables';

$markerWidth: 3px;

body {
    .p-dropdown.p-disabled {
        color: $nokiaGray2Color;
        background-color: $nokiaUiWhiteColor;
        border-color: $nokiaGray2Color;
        opacity: 1;

        .p-inputtext {
            color: $nokiaGray2Color;
        }

        .p-dropdown-trigger {
            color: $nokiaGray2Color;
            background-color: $nokiaUiWhiteColor;
            border-color: $nokiaGray2Color;
        }
    }

    .dropdown-label {
        margin-bottom: 3px;
    }

    .p-dropdown {
        border-radius: 3px;
        width: 100%;

        &:not(.p-disabled):hover {
            &:not(.ui-dropdown-open) {
                border-color: $nokiaLightBlueColor;

                .p-dropdown-trigger {
                    border-color: $nokiaLightBlueColor;
                }
            }
        }

        &:not(.p-disabled):focus-within {
            background-color: $nokiaUiWhiteColor;
            border-color: $nokiaUiGray4Color;

            .p-dropdown-trigger {
                border-color: $nokiaUiGray4Color;
            }
        }

        .p-dropdown-label {
            padding-left: $nokiaDefaultIconPadding;
            text-overflow: ellipsis;
            white-space: nowrap;
            overflow: hidden;
            border: 0;
        }

        .p-dropdown-trigger {
            border-bottom-right-radius: 3px;
            border-top-right-radius: 3px;
            border-left: 1px solid $nokiaGray3Color;
            color: $nokiaUiBlackColor;
            width: 26px;
        }
    }

    .p-dropdown-panel {
        width: inherit;
        padding-bottom: 5px;
        border: none;
        box-shadow: $nokiaActiveBoxShadow;

        .p-dropdown-filter-container {
            width: 100%;
            display: flex;
            padding: 5px 6px;

            .p-inputtext {
                height: auto;
                font-size: $nokiaControlTextFontSize;
                background-color: transparent;
                border: none;
                padding: 0;
                margin: 0;
            }

            .p-dropdown-filter-icon {
                display: none;
            }

            &::before {
                font: normal normal normal 24px/1 'Material Design Icons', sans-serif;
                font-size: 1.5em;
                height: 1em;
                line-height: 1em;
                content: $nokiaMDIFilterIconContent;
                color: $nokiaUiGray4Color;
            }
        }

        .p-dropdown-items {
            font-size: $nokiaControlTextFontSize;
            padding-top: 8px;

            .p-dropdown-item {
                border-left: $markerWidth solid transparent;
                text-overflow: ellipsis;
                overflow: hidden;
                white-space: nowrap;
                padding: 3px 5px;

                &.p-disabled {
                    color: $nokiaGray3Color;
                }

                &:not(.p-highlight):not(.p-disabled):hover {
                    background: $nokiaUiLightBlue1Color;
                    border-color: $nokiaUiLightBlue1Color;
                }

                &.p-highlight {
                    border-color: $nokiaDarkBlueColor;
                    color: $nokiaDarkBlueColor;
                    font-weight: bold;
                    background-color: transparent;
                }
            }

            .p-dropdown-item-group {
                padding: $nokiaDefaultIconPadding;
                font-weight: bold;

                &:not(:first-child) {
                    margin-top: 10px;
                    padding: 15px 8px 3px;
                    border-top: $nokiaDefaultTableCellBorder;
                }

                &:first-child {
                    padding: 0 8px 3px;
                }

                &:not(.p-highlight):not(.p-disabled):hover {
                    background: transparent;
                }
            }
        }
    }

    .p-dropdown-items-wrapper {
        .p-dropdown-items {
            .p-dropdown-item {
                i.mdi {
                    margin-right: 6px;
                }

                .status-available-icon {
                    color: $nokiaUiOkayGreenColor;
                }

                .status-warning-icon {
                    color: $nokiaAlarmYellowColor;
                }

                .status-expired-icon {
                    color: $nokiaAlarmRedColor;
                }
            }
        }
    }
}
