@import '../nokia-variables';

body {
    .ui-togglebutton {
        background-color: transparent;
        border: 0;

        &:not(.p-disabled):not(.ui-state-active):not(:active):hover {
            background-color: #C8C8C8;
            border-color: #C8C8C8;
            color: $nokiaDefaultCheckedTextColor;
        }
    }
}
