@mixin custom-font($fontFamily, $src) {
    @font-face {
        font-family: $fontFamily;
        src: url($src);
    }
}

@include custom-font('Nokia Pure', '/assets/nokia/fonts/NokiaPureText-Regular.ttf');
@include custom-font('Nokia Pure Headline Ultralight', '/assets/nokia/fonts/NokiaPureHeadline_Ultralight.ttf');
@include custom-font('Nokia Pure Headline Light', '/assets/nokia/fonts/NokiaPureHeadline_Light.ttf');
@include custom-font('Nokia Pure Headline', '/assets/nokia/fonts/NokiaPureHeadline_Regular.ttf');
@include custom-font('Nokia Pure Headline Bold', '/assets/nokia/fonts/NokiaPureHeadline_Bold.ttf');
@include custom-font('Nokia Pure Headline ExtraBold', '/assets/nokia/fonts/NokiaPureHeadline_ExtraBold.ttf');

@mixin mdi-size($fontSize) {
    .mdi-#{$fontSize}.mdi-set, .mdi-#{$fontSize}.mdi::before {
        font-size: #{$fontSize};
        line-height: #{$fontSize};
    }
}

@include mdi-size('21px');
@include mdi-size('20px');
@include mdi-size('19px');
@include mdi-size('18px');
@include mdi-size('17px');
@include mdi-size('16px');
@include mdi-size('15px');
@include mdi-size('13px');
