@import '../nokia-variables';

body {
    .p-checkbox {
        width: $nokiaCheckboxHeight;
        height: $nokiaCheckboxHeight;

        .p-checkbox-box {
            width: inherit;
            height: inherit;
            color: $nokiaUiWhiteColor;
            background-color: $nokiaUiWhiteColor;
            border: 1px solid $nokiaGray3Color;
            box-shadow: none;
            border-radius: $nokiaDefaultBorderRadius;
            transition: none;

            ::before {
                font-size: calc(#{$nokiaDefaultTextFontSize});
            }

            &.p-focus {
                border: 1px solid $nokiaUiGray4Color;
                box-shadow: none;
            }

            &.p-highlight {
                background-color: $nokiaToastBlueColor;
                border-color: $nokiaDarkBlueColor;

                ::before {
                    display: block;
                    border-radius: calc(#{$nokiaDefaultBorderRadius} - 1px);
                    font-size: calc(#{$nokiaDefaultTextFontSize});
                    margin: 1px;
                }

                &.p-focus {
                    background-color: $nokiaUiWhiteColor;
                    border-color: $nokiaToastBlueColor;
                    box-shadow: none;

                    ::before {
                        background-color: $nokiaToastBlueColor;
                        margin: 1px;
                    }
                }
            }

            .p-checkbox-icon {
                font-size: calc(#{$nokiaDefaultTextFontSize} + 2px);
                outline: 0 none;
            }
        }
    }

    p-checkbox {
        display: flex;
        align-items: center;
        margin-bottom: 6px;

        &:hover .p-checkbox .p-checkbox-box.p-highlight:not(.p-disabled) {
            background-color: $nokiaDarkerBlueColor;
            border-color: $nokiaDarkerBlueColor;

            ::before {
                background-color: $nokiaDarkerBlueColor;
                margin: 1px;
            }

            .p-checkbox-icon {
                background-color: $nokiaDarkerBlueColor;
                border: $nokiaDarkerBlueColor;
            }
        }

        &:hover .p-checkbox .p-checkbox-box:not(.p-disabled) {
            border-color: $nokiaLightBlueColor;
            box-shadow: none;
        }

        &:active .p-checkbox .p-checkbox-box.p-highlight:not(.p-disabled) {
            border-color: $nokiaDarkerBlueColor;
            box-shadow: $nokiaActiveBoxShadow;
        }

        &:active .p-checkbox .p-checkbox-box:not(.p-disabled) {
            border: 1px solid $nokiaLightBlueColor;
            box-shadow: $nokiaActiveBoxShadow;
        }

        .ui-label-disabled {
            color: $nokiaGray2Color;
        }

        .p-checkbox-label {
            margin: 0;
            padding: 0 0 0 8px;
            cursor: pointer;
            white-space: nowrap;
        }

        .p-checkbox-box.p-disabled {
            background-color: $nokiaUiBackgroundColor;
            border: 1px solid $nokiaGray2Color;
            box-shadow: none;
        }

        .p-checkbox-box.p-highlight.p-disabled {
            background-color: $nokiaGray11Color;
            border-color: $nokiaGray11Color;
        }
    }
}
