@import '../nokia-variables';

body {
    .p-tabview {
        padding: 8px 0 5px 0;

        .p-tabview-panels {
            background-color: $nokiaGray1Color;
            border: none;
            padding: 10px 0;
        }

        .p-tabview-nav {
            margin-left: 15px;
            margin-right: 15px;
            border-bottom: 1px solid $nokiaGray3Color;
            margin-bottom: 0;

            li.p-highlight {
                background-color: $nokiaDarkBlueColor;
                border: 1px solid $nokiaDarkBlueColor;

                &:hover {
                    background-color: $nokiaDarkerBlueColor;
                    border-color: $nokiaDarkerBlueColor;
                }
            }

            li {
                border: 1px solid $nokiaGray3Color;
                background-color: $nokiaUiWhiteColor;
                margin-bottom: 0;
                border-top-left-radius: 3px;
                border-top-right-radius: 3px;
                font-size: 11px;
                position: relative;
                line-height: normal;

                &.p-disabled {
                    background-color: $nokiaUiBackgroundColor;

                    a {
                        color: $nokiaGray2Color;
                    }
                }

                &:not(.p-highlight):not(.p-disabled):hover {
                    background-color: $nokiaUiLightBlue1Color;
                    border-color: $nokiaLightBlueColor;

                    a {
                        color: $nokiaUiGray4Color;
                    }
                }

                &:focus {
                    outline: none;

                    &:not(.p-tabview-selected):not(.p-disabled) {
                        border: 1px solid $nokiaUiGray4Color;

                        &:hover {
                            box-shadow: $nokiaActiveBoxShadow;
                            border: 1px solid $nokiaLightBlueColor;
                        }
                    }
                }

                a {
                    display: block;
                    outline: none;
                    padding: 6px 8px;
                    font-weight: 100;
                    line-height: 1rem;
                    color: $nokiaUiGray4Color;
                }

                span {
                    font-weight: 100;
                }
            }
        }
    }
}

