@import '../nokia-variables';

html {
    font-family: 'Nokia Pure', sans-serif;
    font-size: 12px;
}

body, body .p-component {
    font-family: $nokiaDefaultFontFace;
    font-size: $nokiaDefaultTextFontSize;
}

body .mdi-stack {
    display: inline-block;
    height: 2em;
    line-height: 2em;
    position: relative;
    vertical-align: middle;
    width: 2.5em;
}

body .mdi-stack-1x {
    line-height: inherit;
    left: 0;
    position: absolute;
    text-align: center;
    width: 100%;
}

body .mdi-stack-2x {
    left: 0;
    position: absolute;
    text-align: center;
    width: 100%;
    font-size: 2em;
}

body .mdi-inverse {
    color: white;
}

body .fc .fc-toolbar .p-button .ui-icon-circle-triangle-w {
    font-family: 'PrimeIcons', sans-serif;
    text-indent: 0;
}

body .fc .fc-toolbar .p-button .ui-icon-circle-triangle-e {
    font-family: 'PrimeIcons', sans-serif;
    text-indent: 0;
}
