$minWidth: 230px;

.tile-tooltip.p-tooltip {
    max-width: unset;
    min-width: $minWidth;

    .p-tooltip-arrow {
        margin: 0;
        border-width: 0;
    }

    .p-tooltip-text {
        background-color: $nokiaUiWhiteColor;
        color: $nokiaUiBlackColor;
        border-radius: 0;
        white-space: pre;
        padding: 10px;
        min-width: $minWidth;

        .tooltip-row {
            display: flex;

            .name-column {
                width: 150px;
            }

            &:first-of-type {
                font-weight: $nokiaHeaderFontWeight;
            }
        }
    }
}

.tile-tooltip.p-tooltip-top {
    margin-left: 18px;
}

.p-tooltip .p-tooltip-text {
    box-shadow: 3px 7px 13px rgb(0 0 0 / 30%);
}
