@import '../nokia-variables';

body {
    .ui-accordion {
        .ui-accordion-header {
            &:not(.p-disabled).ui-state-active a {
                background-color: $nokiaDarkBlueColor;
                border: 1px solid $nokiaDarkBlueColor;
                color: $nokiaUiWhiteColor;
            }

            &:not(.p-disabled).ui-state-active:hover a {
                border: 1px solid $nokiaLighterBlueColor;
                background-color: $nokiaLighterBlueColor;
                color: $nokiaUiWhiteColor;
            }
        }
    }
}
