@import 'theme/nokia-variables';

.small-ui-menu {
    &.p-menu {
        min-width: unset;
        width: auto;
        border-radius: $nokiaDefaultBorderRadius;
        box-shadow: $nokiaDropdownMenuBoxShadow;
        line-height: 1;
        padding: 8px 0;
        border: none;
        white-space: nowrap;
        background-color: $nokiaUiWhiteColor;

        > .p-menu-list > .p-menu-separator {
            border-top: 1px solid #98A2AE;
            margin: 0.25rem 8px;
        }

        .p-menuitem-link {
            height: 22px;
            padding: 3px 10px;

            &:focus {
                outline: 1px solid $offBorderColor;
                outline-offset: -1px;
            }

            &:hover {
                background-color: $nokiaUiLightBlue1Color;
            }

            &.p-disabled {
                opacity: 0.5;
                pointer-events: all;
            }

            .p-menuitem-icon {
                margin-right: 6px;
                font-size: 15px;
                display: inline-block;
            }
        }
    }

    .mdi-stack.size.margin {
        margin: 0;
    }
}
